import React from "react";
import ProgressBar from "react-scroll-progress-bar";
import brickit from "./brickit.png";
import { styles } from "../styles";
import one from "./brickitImages/1.png";
import two from "./brickitImages/2.png";
import three from "./brickitImages/3.png";
import four from "./brickitImages/4.png";
import five from "./brickitImages/5.png";

const Brickit = () => {
  return (
    <div className="mt-[100px] pb-10">
      <img src={brickit} alt="" className="w-full" />
      <div className="px-10 mt-8">
        <h1 className={`${styles.proyectosTitle} text-start font-medium`}>
          Brickit Platform
        </h1>
        <h1 className={`font-regular text-grey font-light text-[22px]`}>
          Plataforma web de tokenizacion de inmuebles.Este proyecto fue
          realizado como proyecto final de mi carrera de Ingenieria en Sistemas
          de Informacin.Este sistema cuenta con funcionalidades tales como
          registro de usuario, consulta de portfolio, compra y venta
          peer-to-peer de tokens y consulta de estadisticas
        </h1>
        <div className="px-0 md:px-20 xl:px-20">
          <img src={three} alt="" className="w-full mt-10 rounded-[20px]" />
          <img src={five} alt="" className="w-full mt-10 rounded-[20px]" />
          <img src={one} alt="" className="w-full mt-10 rounded-[20px]" />
          <img src={two} alt="" className="w-full mt-10 rounded-[20px]" />
          <img src={four} alt="" className="w-full mt-10 rounded-[20px]" />
        </div>
      </div>
    </div>
  );
};

export default Brickit;
