import React from "react";
import yo from "./yo.png";
import { Icon } from "@iconify/react";
import { fadeIn, textVariant } from "../utils.js";
import { motion } from "framer-motion";

const Landing = () => {
  return (
    <div className="flex min-w-screen min-h-screen justify-center items-center" id="home">
      <div className="flex flex-col w-full h-full  justify-center items-center">
        <motion.img
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.9 }}
          src={yo}
          alt=""
          className="rounded-full w-[100px] wh-[100px]  md:w-[200px] md:wh-[200px] sm:w-[200px] sm:wh-[200px] lg:w-[250px] xl:w-[300px] "
        />
        <motion.h1 className="degradetext font-regular font-bold mt-6 text-3xl">
          Hola, soy Facundo.
        </motion.h1>
        <h1 className="font-regular font-medium text-lg text-center mt-6 w-[250px] md:w-[300px] lg:w-[400px] text-grey">
          Estudiante de 5ao de Ingenieria en Sistemas de Informacion y
          desarrollador front end.
        </h1>
        <a href="#contacto" className="degrade  text-white font-regular font-normal text-[20px] cursor-pointer py-2 px-8 mt-8 flex flex-row justify-between items-center">
          Contacto
          <Icon
            icon="material-symbols:arrow-forward-rounded"
            className="ml-2"
          />
        </a>
      </div>
    </div>
  );
};

export default Landing;
