import React, { useState } from "react";
import { styles } from "../styles";
import { Icon } from "@iconify/react";
import { motion } from "framer-motion";

const Navbar = () => {
  const [open, setOpen] = useState(false);
  return (
    <>
      {!open && (
        <div className="flex flex-row w-full justify-between items-center px-6 py-2 md:px-20 md:py-5 absolute">
          <a
            href="#home"
            className="font-regular font-bold text-[30px] cursor-pointer tracking-tight"
          >
            Facundo
          </a>
          <div className="md:flex md:flex-row justify-between items-center hidden">
            <a href="#personal" className={styles.navbtn}>
              Personal
            </a>
            <a href="#habilidades" className={styles.navbtn}>
              Habilidades
            </a>
            <a href="#proyectos" className={styles.navbtn}>
              Proyectos
            </a>
            <a
              href="#contacto"
              className={`${styles.navbtn} rounded-[20px] border-2 border-black py-1 px-6 flex flex-row justify-between items-center hover:bg-black hover:text-white transition-all	`}
            >
              Contacto
              <Icon
                icon="material-symbols:arrow-forward-rounded"
                className="ml-2"
              />
            </a>
          </div>
          <button
            className="flex md:hidden"
            onClick={() => {
              setOpen(!open);
            }}
          >
            {!open && <Icon icon="material-symbols:menu-rounded" height={30} />}
          </button>
        </div>
      )}

      {open && (
        <div className="absolute w-screen h-screen flex flex-col items-center bg-[#fff]">
          <div className="flex flex-row w-full justify-between items-center px-6 py-2 md:px-20 md:py-5">
            <a
              href="#home"
              className="font-regular font-bold text-[30px] cursor-pointer tracking-tight"
            >
              Facundo
            </a>
            <button
              className="flex md:hidden"
              onClick={() => {
                setOpen(!open);
              }}
            >
              <Icon icon="material-symbols:close" height={30} />
            </button>
          </div>
          <div className="w-full h-full flex flex-col justify-center items-center">
            <div className="flex flex-col justify-center items-center justify-between w-full h-1/3">
              <a
                href="#personal"
                className="font-regular font-bold text-[27px] cursor-pointer "
                onClick={() => {
                  setOpen(!open);
                }}
              >
                Personal
              </a>
              <a
                href="#habilidades"
                className="font-regular font-bold text-[27px] cursor-pointer "
                onClick={() => {
                  setOpen(!open);
                }}
              >
                Habilidades
              </a>
              <a
                href="#proyectos"
                className="font-regular font-bold text-[27px] cursor-pointer "
                onClick={() => {
                  setOpen(!open);
                }}
              >
                Proyectos
              </a>
              <a
                href="#contacto"
                className="font-regular font-bold text-[27px] cursor-pointer "
                onClick={() => {
                  setOpen(!open);
                }}
              >
                Contacto
              </a>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default Navbar;
