import React from 'react'
import instagram from "./instagram.png";
import { styles } from "../styles";
import instagramVideo from './instagramVideo.mp4'

const Instagram = () => {
  return (
    <div className="mt-[100px] pb-20">
    <img src={instagram} alt="" className="w-full" />
    <div className="px-10 mt-8">
      <h1 className={`${styles.proyectosTitle} text-start font-medium`}>
      Instagram Clone      </h1>
      <h1 className={`font-regular text-grey font-light text-[22px]`}>
      Plataforma web para una consultora de recursos humanos de la ciudad de Crdoba.Permite la gestion y evaluacion del personal
      </h1>
    </div>
    <div className="px-10 w-[400px] h-[400px] mt-10 pb-20">
    <video controls>
        <source src={instagramVideo} type="video/mp4" />
        Tu navegador no soporta el elemento de video.
      </video>
      </div>
  </div>
  )
}

export default Instagram