import React from "react";
import { styles } from "../styles";
import { Icon } from "@iconify/react";
import pen from "./pen.png";
import cellphone from "./cellphone.png";
import tag from "./tag.png";
import { motion } from "framer-motion";
import { useInView } from "react-intersection-observer";

const Habilidades = () => {
  const { ref, inView } = useInView({
    threshold: 0.1,
  });
  return (
    <div className="flex flex-col  w-full min-h-screen items-center justify-center px-20 pt-20  md:pt-0" id="habilidades">
      <motion.h1
        initial="hidden"
        whileInView="show"
        variants={{
          hidden: {
            y: 50,
            opacity: 0,
          },
          show: {
            y: 0,
            opacity: 1,
            transition: {
              type: "spring",
              duration: 3,
              delay: 0.2,
            },
          },
        }}
        className={`degradetext ${styles.title} mb-20`}
      >
        Mis habilidades
      </motion.h1>
      <div className="flex 2xl:flex-row flex-col justify-between items-center w-full">
        <motion.div
          initial="hidden"
          whileInView="show"
          variants={{
            hidden: {
              y: 150,
              opacity: 0.1,
            },
            show: {
              y: 0,
              opacity: 1,
              transition: {
                type: "spring",
                duration: 1,
                delay: 0.5,
              },
            },
          }}
          className="flex flex-col items-center justify-between mb-20 2xl:mb-2"
        >
          <img src={pen} alt="" />
          <h1 className={`${styles.habilidadesTitle} mr-2`}>Diseo UX/UI</h1>
          <h1 className={styles.habilidadesText}>
            Desarrollo interfaces en Figma y Photoshop a partir de los
            requerimientos de los clientes, creando soluciones atractivas y
            funcionales{" "}
          </h1>
        </motion.div>
        <motion.div
          initial="hidden"
          whileInView="show"
          variants={{
            hidden: {
              y: 150,
              opacity: 0,
            },
            show: {
              y: 0,
              opacity: 1,
              transition: {
                type: "spring",
                duration: 1,
                delay: 0.7,
              },
            },
          }}
          className="flex flex-col items-center justify-center mb-20 2xl:mb-0"
        >
          <div className="flex flex-row">
            <img src={tag} alt="" />
          </div>
          <h1 className={`${styles.habilidadesTitle}`}>Desarrollo web</h1>
          <h1 className={styles.habilidadesText}>
            Desarrollo utilizando HTML, CSS y JavaScript. Me siento cmodo
            trabajando con bibliotecas como React y utilizando herramientas como
            Tailwind para dar estilo a mis proyectos.
          </h1>
        </motion.div>
        <motion.div
          initial="hidden"
          whileInView="show"
          variants={{
            hidden: {
              y: 150,
              opacity: 0,
            },
            show: {
              y: 0,
              opacity: 1,
              transition: {
                type: "spring",
                duration: 1,
                delay: 1,
              },
            },
          }}
          className="flex flex-col items-center justify-center mb-20 2xl:mb-0"
        >
          <img src={cellphone} alt="" />
          <h1 className={`${styles.habilidadesTitle} ml-2`}>
            Desarrollo mobile
          </h1>
          <h1 className={styles.habilidadesText}>
            Desarrollo utilizando tecnologas nativas. Soy capaz de crear
            aplicaciones mviles atractivas y funcionales utilizando React
            Native, y puedo integrar diferentes APIs y servicios en ellas
          </h1>
        </motion.div>
      </div>
    </div>
  );
};

export default Habilidades;
