import React from "react";
import { styles } from "../styles";
import Card from "./Card";
import { fadeIn, textVariant } from "../utils.js";
import { motion } from "framer-motion";
import { useInView } from "react-intersection-observer";

const Personal = () => {
  const { ref, inView } = useInView({
    threshold: 0.1,
  });

  const animateTitulo = {
    y: inView ? 0 : 100,
    opacity: inView ? 1 : 0,
  };

  return (
    <div className="flex flex-col w-full min-h-screen items-center justify-center pt-10 overflow-hidden" id="personal">
      <motion.h1
        initial="hidden"
        whileInView="show"
        variants={{
          hidden: {
            y: -50,
            opacity: 0.1,
          },
          show: {
            y: 0,
            opacity: 1,
            transition: {
              type: "spring",
              duration: 3,
              delay: 0.2,
            },
          },
        }}
        className={`degradetext text-black ${styles.title} mb-20`}
      >
        Acerca de mi
      </motion.h1>
      <Card></Card>
    </div>
  );
};

export default Personal;
