import React from "react";
import car from "./car.png";
import rio from "./rio.png";
import land from "./land.png";
import { motion } from "framer-motion";
import { useInView } from "react-intersection-observer";

const Card1 = () => {
  const { ref, inView } = useInView({
    threshold: 0.1,
  });
  const animateTitulo = {
    x: inView ? 0 : -100,
    opacity: inView ? 1 : 0,
  };
  return (
    <motion.div
      ref={ref}
      initial={{ opacity: 0, x: -100 }}
      animate={animateTitulo}
      transition={{ duration: 1.2 }}
      className="card flex flex-col sm:flex-row items-center justify-between w-[300px] h-auto pt-4 sm:pt-0 sm:w-[356px] sm:h-[163px] md:w-[499px] md:h-[228px] xl:w-[712px] xl:h-[326px] mb-20"
    >
      <div className="flex w-full h-ful items-center justify-center">
        <h1 className="text-center w-[300px] font-regular text-[20px] md:text-[25px] font-medium text-grey mb-4 sm:mb-0">
          Mi nombre es Facundo, soy un desarrollador frontend de Crdoba
        </h1>
      </div>
      <img
        src={car}
        className="sm:rounded-r-[20px] rounded-b-[20px] sm:rounded-bl-[0px] h-full w-[300px] "
      ></img>
    </motion.div>
  );
};

const Card2 = () => {
  const { ref, inView } = useInView({
    threshold: 0.1,
  });

  const animateTitulo = {
    x: inView ? 0 : 100,
    opacity: inView ? 1 : 0,
  };

  return (
    <motion.div
      ref={ref}
      initial={{ opacity: 0, x: 100 }}
      animate={animateTitulo}
      transition={{ duration: 1.2 }}
      className="card flex flex-col sm:flex-row items-center justify-between w-[300px] h-auto sm:w-[356px] sm:h-[163px] md:w-[499px] md:h-[228px] xl:w-[712px] xl:h-[326px] mb-20"
    >
      <img
        src={land}
        className="rounded-t-[20px] rounded-b-[0px] rounded-bl-[0px] sm:rounded-l-[20px] sm:rounded-r-[0px] h-full w-[300px]"
      ></img>
      <div className="flex w-full h-ful items-center justify-center">
        <h1 className="text-center w-[300px] font-regular text-[20px] md:text-[25px] font-medium text-grey mb-4 sm:mb-0">
          Actualmente estoy cursando el ltimo ao de la carrera de Ingeniera
          en Sistemas de Informacin.
        </h1>
      </div>
    </motion.div>
  );
};

const Card3 = () => {
  const { ref, inView } = useInView({
    threshold: 0.1,
  });
  const animateTitulo = {
    x: inView ? 0 : -100,
    opacity: inView ? 1 : 0,
  };
  return (
    <motion.div
      ref={ref}
      initial={{ opacity: 0, x: -100 }}
      animate={animateTitulo}
      transition={{ duration: 1.2 }}
      className="card flex flex-col sm:flex-row items-center justify-between w-[300px] h-auto pt-4 sm:pt-0 sm:w-[356px] sm:h-[163px] md:w-[499px] md:h-[228px] xl:w-[712px] xl:h-[326px]"
    >
      <div className="flex w-full h-ful items-center justify-center">
        <h1 className="text-center w-[300px] font-regular text-[20px] md:text-[25px] font-medium text-grey mb-4 sm:mb-0">
          Me apasiona el desarrollo web y mobile utilizando tecnologas como
          React y React Native
        </h1>
      </div>
      <img
        src={rio}
        className="sm:rounded-r-[20px] rounded-b-[20px] sm:rounded-bl-[0px] h-full w-[300px]"
      ></img>
    </motion.div>
  );
};

const Card = () => {
  const { ref, inView } = useInView({
    threshold: 0.1,
  });
  const { ref2, inView2 } = useInView({
    threshold: 0.1,
  });
  const { ref3, inView3 } = useInView({
    threshold: 0.1,
  });

  const animateTitulo = {
    x: inView ? 0 : -100,
    opacity: inView ? 1 : 0,
  };

  const animateTitulo2 = {
    x: inView ? 0 : 100,
    opacity: inView ? 1 : 0,
  };

  const animateTitulo3 = {
    x: inView3 ? 0 : -100,
    opacity: inView3 ? 1 : 0,
  };

  return (
    <div className="flex flex-col w-full items-center">
      <Card1 />
      <Card2 />
      <Card3 />
    </div>
  );
};

export default Card;
