import React from "react";
import recursos from "./recursos.png";
import { styles } from "../styles";
import one from "./recursosImages/1.png";
import two from "./recursosImages/2.png";
import three from "./recursosImages/3.png";
import four from "./recursosImages/4.png";

const RecursosHumanos = () => {
  return (
    <div className="mt-[100px] pb-10">
      <img src={recursos} alt="" className="w-full" />
      <div className="px-10 mt-8">
        <h1 className={`${styles.proyectosTitle} text-start font-medium`}>
          Plataforma de RRHH
        </h1>
        <h1 className={`font-regular text-grey font-light text-[22px]`}>
          Plataforma web para una consultora de recursos humanos de la ciudad de
          Crdoba.Permite generar plantillas de evaluacion del personal,
          realizar evaluaciones por cada empleado de la empresa como asi tambien
          revisar sus estadisticas
        </h1>
        <div className="px-0 md:px-20 xl:px-20">
          <img src={three} alt="" className="w-full mt-10 rounded-[20px]" />
          <img src={one} alt="" className="w-full mt-10 rounded-[20px]" />
          <img src={two} alt="" className="w-full mt-10 rounded-[20px]" />
          <img src={four} alt="" className="w-full mt-10 rounded-[20px]" />
        </div>
      </div>
    </div>
  );
};

export default RecursosHumanos;
